<!-- 报表-商品库存 -->
<template>
	<view> <cu-custom :bgColor="NavBarColor" :isBack="true" backRouterName="index">
							<block slot="backText">返回</block>
							<block slot="content">商品库存列表</block>
		</cu-custom>
		<scroll-view>
			<div>
				<hr>
				<view class="stock-list" :style="[{animation: 'show 0.5s 1'}]">
					<view class="stock-item" v-for="stock in stocks" :key="stock.name">
						<view class="item-row">
							<text class="label">名称：</text>
							<text class="value name">{{ stock.name || '-' }}</text>
							<text class="label">类别：</text>
							<text class="value">{{ stock.categoryName || '-' }}</text>
						</view>
						<view class="item-row">
							<text class="label">采购价：</text>
							<text class="value">{{ stock.purchaseDecimal.toFixed(2) || '-' }}</text>
							<text class="label">单位：</text>
							<text class="value">{{ stock.unit || '-' }}</text>
						</view>
						<view class="item-row">
							<text class="label">库存数量：</text>
							<text class="value">{{stock.currentStock || '-' }}</text>
							<text class="label">库存金额：</text>
							<text class="value">¥{{stock.currentStockPrice.toFixed(2) || '-' }}</text>
						</view>
					</view>
				</view>
			</div>
			<hr>
			<view class="sum_stock-item" :style="[{animation: 'show 0.5s 1'}]">
			<text class="label">合计：		总库存：</text>
			<text class="sumvalue">{{currentStock|| '-'}}</text>
			<text class="label">			总金额：</text>
			<text class="sumvalue">¥{{currentStockPrice|| '-'}}</text>
		</view>
		</scroll-view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				stocks: [],
				        currentStockPrice:'',
				        currentStock:'',
				stockUrl: '/material/getListWithStock'
			}
		},
		onLoad() {
			this.loadinfo()
		},
		methods: {

			loadinfo() {
				this.$http.get(this.stockUrl, {
					params: {
						currentPage: 1,
						pageSize: 99,
						materialParam: '',
						zeroStock: 0
					}
				}).then(res => {
					if (res.data.code == "200") {
						let result = res.data.data;
						this.currentStock=result.currentStock;
						this.currentStockPrice=result.currentStockPrice;
						this.stocks = result.rows;
					}
				}).catch(e => {
					console.log("请求错误", e)
				})
			}
		}
	}
</script>

<style scoped>
	.stock-list {
		border-radius: 5px;
		overflow: hidden;
	}

	.stock-item {
		background: #fff;
		padding: 3px;
		margin-bottom: 5px;
		border-radius: 5px;
		box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
	}

	.item-row {
		display: flex;
		margin-bottom: 6px;
		font-size: 14px;
	}

	.label {
		color: #000000;
		width: 80px;
		font-family: 'HarmonyOS Sans Black';
		text-align: right;
	}

	.value {
		color: #ff0000;
		flex: 1;
		font-size: 10px;
		margin-top: 2px;
		text-align: left;
	}	
	.name {
		font-weight: bold;
		color: #0000ff;
	}
	.sumvalue {
		color: #ff0000;
		flex: 1;
		font-size: 16px;
		margin-top: 2px;
		margin-right: 20px;
		text-align: left;
	}
	.sum_stock-item {
		background: #06f0be;
		padding: 3px;
		margin-bottom: 5px;
		border-radius: 5px;
		box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
	}
</style>